import React from 'react'
import { connect } from 'react-redux'

import controller from '../controller/index'

import Tag from './Tag'

function DetailBoxAddition({
  createTime,
  tags,
  repostNotice,
  lang,
}) {
  return (
    <div style={{
      textAlign: 'right',
      paddingTop: '20px',
      fontSize: '14px'
    }}>
      <div style={{
        padding: '5px 0 5px 0'
      }}>
        {
          tags && tags.map((tag, index) => <Tag key={index} active children={tag} />)
        }
      </div>

      <div style={{
        padding: '5px 0 5px 0'
      }}>
        <b>
          {
            ({
              zh: ``,
              en: `Created Time:`,
            })[lang]
          }&nbsp;&nbsp;</b>
        <span>{createTime}</span>
      </div>

      <div style={{
        padding: '5px 0 5px 0'
      }}>
        <b>
          {
            ({
              zh: ``,
              en: `Reprinting Note:`,
            })[lang]
          }
          &nbsp;&nbsp;</b>
        <span>
          {
            ({
              zh: ``,
              en: `This article is original, reprint please indicate the source`,
            })[lang]
          }
        </span>
        {/*  */}
      </div>
    </div>
  )
}


export default connect(
  (state, ownProps) => {
    return {
      lang: state.innerState.lang,
    }
  },
  (dispatch, ownProps) => {
    return {

    }
  }
)(DetailBoxAddition)


